import Vue from 'vue'
import Router from 'vue-router'

import Login from '../pages/Login.vue'
import Welcome from '../pages/Welcome'
import Navigation from '../pages/Navigation'
import Main from '../pages/Main'


import Info from '../pages/account/Info'
import InfoMain from '../pages/account/InfoMain'
import InfoUpdate from '../pages/account/InfoUpdate'
import InfoFocusPeo from '../pages/account/InfoFocusPeo'
import InfoDynamic from '../pages/account/InfoDynamic'
import InfoCollection from '../pages/account/InfoCollection'
import InfoNotes from '../pages/account/InfoNotes'
import InfoFeedback from '../pages/account/InfoFeedback'
import InfoSetting from '../pages/account/InfoSetting'
import InfoFoot from '../pages/account/InfoFoot'
import InfoLevel from '../pages/account/InfoLevel'
import InfoRecord from '../pages/account/InfoRecord'


import Problem from "../pages/problem/Problem";
import ProblemPractice from "../pages/problem/ProblemPractice";
import ProblemInterview from "../pages/problem/ProblemInterview";
import ProblemBasis from "../pages/problem/ProblemBasis";
import ProblemAlgorithm from "../pages/problem/ProblemAlgorithm";
import ProblemDatabase from "../pages/problem/ProblemDatabase";
import ProblemLanguage from "../pages/problem/ProblemLanguage";


import Circle from "../pages/circle/Circle";


import Fight from "../pages/fight/Fight";

Vue.use(Router)
let routes = [
  {path: '/', component: Welcome, name: ''},
  {path: '/login', component: Login, name: ''},
  {path: '/navigation',
    redirect: '/account/info',
    component: Navigation,
    name: '',
    children: [
      {path: '/main', component: Main, name: ''},
      {path: '/fight', component: Fight, name: ''},
      {path: '/problem', component: Problem, name: ''},
      {path: '/practice',
        component: ProblemPractice,
        name: '',
        redirect: '/practice/basic',
        children: [
          {path: '/practice/basic', component: ProblemBasis, name: ''},
          {path: '/practice/algorithm', component: ProblemAlgorithm, name: ''},
          {path: '/practice/database', component: ProblemDatabase, name: ''},
          {path: '/practice/language', component: ProblemLanguage, name: ''},
        ]},
      {path: '/interview', component: ProblemInterview, name: ''},
      {path: '/circle', component: Circle, name: ''},
      {path: '/account/info',
        redirect: '/account/main',
        component: Info,
        name: '',
        children: [
          {path: '/account/main', component: InfoMain, name: ''},
          {path: '/account/infoUpdate', component: InfoUpdate, name: ''},
          {path: '/account/infoFocusPeo', component: InfoFocusPeo, name: ''},
          {path: '/account/infoCollection', component: InfoCollection, name: ''},
          {path: '/account/infoDynamic', component: InfoDynamic, name: ''},
          {path: '/account/infoNotes', component: InfoNotes, name: ''},
          {path: '/account/infoFeedback', component: InfoFeedback, name: ''},
          {path: '/account/infoSetting', component: InfoSetting, name: ''},
          {path: '/account/infoFoot', component: InfoFoot, name: ''},
          {path: '/account/infoLevel', component: InfoLevel, name: ''},
          {path: '/account/infoRecord', component: InfoRecord, name: ''}
        ]}
    ]},
  {path: '*', redirect: '/404', name: '404'}
]

export default routes
